body {
  background: #fff; }

.App {
  text-align: center;
  margin: 5%; }
  .App .nav {
    width: 100%;
    padding: 0;
    margin: 10px auto;
    list-style: none; }
    .App .nav button {
      margin-right: 10px; }
    .App .nav a {
      color: #fff;
      text-decoration: none; }
  .App .cont {
    padding: 10px;
    -webkit-box-shadow: 0px 3px 40px #ddd;
            box-shadow: 0px 3px 40px #ddd; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }



