.Map {
  margin: 0 auto;
  width: 100%;
  height: 300px; }

.popup {
  position: relative;
  left: -50px;
  width: 100px;
  height: 100px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  color: #fff;
  font-size: 24px;
  text-align: center;
  line-height: 100px; }
  .popup:after {
    content: "";
    position: absolute;
    bottom: -10px;
    border-top: 10px solid rgba(0, 0, 0, 0.7);
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 50%; }

.list {
  width: 300px; }
  .list .placeList {
    cursor: pointer;
    margin-bottom: 5px; }

.component {
  display: -ms-flexbox;
  display: flex;
  width: 90%;
  margin: 10px auto; }



